<template>
	<div id="addres">
    <!--  -->
    <router-link to="/allc/city" class="header"> 
      <input type="text" :placeholder="address.inp" disabled>
    </router-link>
    <div class="pos">
      <span></span>
      <p>{{address.text1}}</p>
    </div>
    <div class="addr">
      {{address.text2}}
      <br>
      {{address.text3}}
    </div>    
  </div>
</template>
<script>
	export default {
		name:'addres',
		data:function(){
            return{
               address:{
                  text1:"",
                  text2:"",
                  text3:"",
                  inp:""
               }
            }
        }
	}
</script>
<style lang="scss">
  @import "../assets/css/addres.scss";
</style>


