<template>
	<div id="cart">
        <div class="cart-d">
            <div class="ul">
                <p>{{text1}}~<br>{{text2}}<br></p>
                <router-link to="/mine/loginin" class="ul-a">{{text3}}</router-link>
            </div>
        </div>
        <!-- end -->
        <div class="footer">
            <foot  v-for="(item,index) in footItems" :key="index" :className="item.className" :text="item.text" :to="item.to"/>
        </div>
    </div>
</template>
<script>
    import foot from "./allc/foot";
	export default {
        name:"cart",
        props:[],
        data:function(){
            return{
                text1:"",
                text2:"",
                text3:"",
                footItems:[
                   {className:"index",text:"",to:"/allc"},
                   {className:"classify",text:"",to:"/classify"},
                   {className:"cartList",text:"",to:"/cart"},
                   {className:"orderList",text:"",to:"/order"},
                   {className:"mine",text:"",to:"/mine"}
                ],
            }
        },
        components:{
            foot
        }
	}
</script>
<style lang="scss">
	@import "../assets/css/cart.scss"
</style>



