<template>
 <!--  -->
  <div id="login">
        <!--  -->
        <div class="header">
            <!--  -->
            <router-link to="/mine/loginin" class="header-sp1">
                <span></span>
            </router-link>
            <router-link to="/mine/loginin" class="header-sp2"></router-link>
        </div>
        <div class="login-top">
            <span class="login-top-sp1"></span>
            <router-link to="/mine/loginin" class="login-top-sp2">{{logins.text}}</router-link>
        </div>
        <div class="login-top-d">
        <!--  -->
        <ul class="login-top-ul">
            <navl v-for="(item,index) in navs" :key="index" :className="item.className" :ico="item.ico" :text="item.text" :href="item.href" />
        </ul>
        </div>
        <div class="login-pic">
            <img :src="logins.src">
        </div>
        <div class="login-list">
            <tub v-for="(item,index) in tubs" :key="index" :href="item.href" :src="item.src" :text="item.text"/>
        </div>
        <div class="close">
            <a href="#">{{logins.text2}}</a>
        </div>
        <!-- end -->
        <div class="footer">
          <foot  v-for="(item,index) in footItems" :key="index" :className="item.className" :text="item.text" :to="item.to"/>
        </div>
    </div>
</template>
<script>
  import swiper from '../assets/swiper/swiper.min.js';
  import foot from "./allc/foot";
  import navl from "./mine/navl";
  import tub from "./mine/tub";
  export default{
     name:"mine",
     // 
     data:function(){
        return {
          navs:[
           {className:"li-top-asp",ico:"",text:"",href:""},
           {className:"li-top-aspb",ico:"--",text:"",href:""},
           {className:"li-top-aspb",ico:"--",text:"",href:""}
          ],
          logins:{text:"/",text2:"",src:"/static/uploads/l0.png"},
          tubs:[
           {src:"/static/img/l1.png",text:"",href:""},
           {src:"/static/img/l2.png",text:"",href:""},
           {src:"/static/img/l3.png",text:"",href:""},
           {src:"/static/img/l4.png",text:"/",href:""},
           {src:"/static/img/l5.png",text:"",href:""},
           {src:"/static/img/l6.png",text:"",href:""},
           {src:"/static/img/l7.png",text:"",href:""},
           {src:"/static/img/l8.png",text:"",href:""},
           {src:"/static/img/l9.png",text:"",href:""},
           {src:"/static/img/l10.png",text:"",href:""}
          ],
          footItems:[
           {className:"index",text:"",to:"/allc"},
           {className:"classify",text:"",to:"/classify"},
           {className:"cartList",text:"",to:"/cart"},
           {className:"orderList",text:"",to:"/order"},
           {className:"mine",text:"",to:"/mine"}
          ],
        }
     },
     // 
     components:{
       foot,
       navl,
       tub
     },
  }
</script>
<style lang="scss">
  @import "../assets/css/login.scss";
</style>


