<template>
    <!--  -->
	<div id="loginin">
        <!--  -->
        <div class="header">
           <span class="header-arrow" @click="bac"></span>
           <span class="header-dj">{{loginins.txt1}}</span>
        </div>
        <!--  -->
        <div class="login-tab">
            <div class="login-tab-left"  @click="showa">
                <span :class="{'login_tab_active':show1==true}">{{loginins.txt2}}</span>
            </div>
            <div class="login-tab-right" @click="showb">
                <span :class="{'login_tab_active':show2==true}">{{loginins.txt3}}</span>
            </div>
        </div>  
        <!--  -->
        <div class="center">
            <!--  -->
            <div class="center-inp-active" v-if="show1"> 
                <div class="inp-container">
                    <div class="inp-txt">{{loginins.txt4}}</div>
                    <input type="text" class="txt
                    " :placeholder="loginins.inp1">
                </div>
                <div class="inp-container">
                    <div class="inp-txt">{{loginins.txt5}}</div>
                    <input type="password" :placeholder="loginins.inp2" class="psd">
                    <span class="psd-visible">
                        <span></span>
                    </span>
                </div>
            </div>
            <!--  -->
            <div class="login-sms" v-if="show2">
                <div class="input-container">
                    <span class="input-sms-sp">
                        <span class="input-sms-sp1">{{loginins.txt6}}</span>
                        <span class="input-sms-sp2"></span>
                    </span>
                    <input type="text" class="sms-input1" :placeholder="loginins.inp3">
                    <button>{{loginins.txt7}}</button>
                </div>
                <div class="input-container">
                    <input type="text" class="sms-input2" :placeholder="loginins.inp4">
                </div>
           </div>
           <!--  -->
            <div class="notice">&nbsp;</div>
            <a href="#" class="dl">{{loginins.txt8}}</a>
            <a href="#" class="on-dl">{{loginins.txt9}}</a>
            <div class="turn-reg">
                <a href="#" class="lose-psd">
                    <span class="lose-psd-sp1"></span>
                    <router-link to="/mine/forget" class="lose-psd-sp2">{{loginins.txt10}}</router-link>
                </a>
                <a href="#" class="quick-reg">
                    <span class="quick-reg-sp1"></span>
                    <router-link to="/mine/regis" class="quick-reg-sp2">{{loginins.txt11}}</router-link>
                </a>
            </div>
            <!--  -->
            <div class="other-dl">
                <h4>{{loginins.txt12}}</h4>
                <a href="#" class="other-dl-a1">
                    <span></span>
                </a>
                <a href="#" class="other-dl-a2">
                    <span></span>
                </a>
                <div class="last-p">
                    <p>
                        {{loginins.txt12}}
                        <a href="//in.m.jd.com/help/app/private_policy.html">{{loginins.text}}</a>
                    </p>
                </div>
            </div>
            
        </div>
        
    </div>
</template>
<script>
	export default{
		name:'',
		props:[],
		data(){
			return{
                show1:true,
                show2:false,
                loginins:{
                    txt1:"",
                    txt2:"",
                    txt3:"",
                    txt4:"",
                    txt5:"",
                    txt6:"+86",
                    txt7:"",
                    txt8:" ",
                    txt9:"",
                    txt10:"",
                    txt11:"",
                    txt12:"",
                    text:"",
                    inp1:"//",
                    inp2:"",
                    inp3:"",
                    inp4:""
                }
            }
		},
        methods:{
            // 
            showa:function(){
                this.show1=true;
                this.show2=false;
            },
            // 
            showb:function(){
                this.show2=true;
                this.show1=false;
            },
            //
            bac:function(){
                this.$router.go(-1);
            }
        }
	}
</script>
<style lang="scss">
	@import "../assets/css/loginin.scss";
</style>


